import React from "react"
import Layout from "../components/Layout"
import Image from "gatsby-image"
import { graphql, useStaticQuery } from "gatsby"

const GetProfile = graphql`
  {
    profile: file(relativePath: { eq: "profile.jpg" }) {
      childImageSharp {
        fluid  (quality:90) {
          ...GatsbyImageSharpFluid
        }
      }
    }
    test: file(relativePath: { eq: "test.jpg" }) {
      childImageSharp {
        fluid (quality:90) {
          ...GatsbyImageSharpFluid
        }
      }
    }
}
`

const About = () => {
  const data1 = useStaticQuery(GetProfile)
  return (
    <Layout>
      <div className="aboutcontainer">
        <Image
          className="profilephoto"
          fluid={data1.test.childImageSharp.fluid}
        />
        <Image
          className="aboutme"
          fluid={data1.profile.childImageSharp.fluid}
        ></Image>
        <p className="mystory">test</p>
      </div>
    </Layout>
  )
}

export default About
